.promo {
  position: relative;
  padding: 158px 40px 40px 40px;
  color: $basic-white;
}

.promo__free-services {
  @include font(Gilroy, 500, normal, 28px, 47px, $basic-white );
  letter-spacing: -1px;
  margin: 0 0 19px 0;
}

.promo__title {
  @include font(Gilroy, 800, normal, 120px, 94px, $basic-white );
  letter-spacing: -5px;
  margin: 0 0 41px 0;
  transform: translateX(-4px);
}

.promo__description {
  position: relative;
  @include font(Gilroy, 700, normal, 42px, 47px, $basic-white );
  letter-spacing: -2px;
  max-width: 440px;
  padding-left: 77px;
  margin: 0 0 41px 0;
}

.promo__description:before {
  position: absolute;
  content: '';
  width: 65px;
  height: 4px;
  top: 23px;
  left: 0;
  background-color: $basic-accent;
  border: 0 solid #ffffff;
}

.promo__call-to-actions {
  display: flex;
  flex-wrap: wrap;
  max-width: 600px;
  margin-bottom: 135px;
}

.promo__call-to-actions button {
  height: 72px;
  min-width: 275px;
  line-height: normal;
  letter-spacing: normal;
  margin-bottom: 51px;
}

.promo__call-to-actions .button--callback {
  margin-left: 21px;
}

.promo__call-to-actions .button--yellow {
  position: relative;
}

.promo__call-to-actions .button--yellow:before {
  position: absolute;
  content: '';
  background-image: url("./../img/arr-garanty.png");
  width: 74px;
  height: 56px;
  bottom: -83px;
  left: 121px;
}

.promo__ads-call {
  @include font(Gilroy, 500, normal, 16px, normal, $basic-white );
  margin: 0 0 0 209px;
  max-width: 240px;
}

.promo__work-area {
  position: relative;
  @include font(Gilroy, 500, normal, 20px, 47px, $basic-accent );
  background-color: rgba($basic-black, 0.7);
  max-width: 565px;
  letter-spacing: -0.04em;
  margin: 0 0 90px 0;
  padding: 7px 0 5px 65px;
}

.promo__work-area:before {
  position: absolute;
  content: '';
  background-image: url("./../img/pin.png");
  width: 23px;
  height: 32px;
  top: 13px;
  left: 19px;
}

.promo__scroll-down {
  @include font(Gilroy, 500, normal, 16px, 34px, $scroll-down );
  position: absolute;
  content: '';
  bottom: 426px;
  left: -195px;
  letter-spacing: -0.02em;
  transform: rotate(-90deg);
}

.promo__scroll-down:before {
  position: absolute;
  content: '';
  width: 2px;
  height: 332px;
  bottom: -149px;
  left: -184px;
  background-color: rgba($basic-white, 0.25);
  transform: rotate(-90deg);
}

.promo__about {
  @include font(Gilroy, 500, normal, 48px, normal, $basic-white );
  letter-spacing: -1px;
  margin: 0 0 12px 0;
  max-width: 1000px;

  & span {
    border-bottom: 4px dotted $basic-accent;
  }
}
