.cupboards-types {
  background: $cupboards-background;
}

.cupboards-types__wrapper {
  max-width: 1920px;
  margin: 0 auto;
}

.cupboards-types__list {
  display: flex;
  max-width: 1920px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.cupboards-types__text-wrapper {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: end;
  width: 100%;
  padding: 0 31px 0 39px;
  bottom: 50px;
  left: 0;
}

.cupboards-types__heading {
  @include font(Gilroy, bold, normal, 20px, normal, $basic-white );
  letter-spacing: -0.02em;
  max-width: 171px;
  margin: 0;
}

.cupboards-types__price {
  @include font(Gilroy, 500, normal, 16px, normal, $basic-white );
  max-width: 148px;
  margin: 0;
  display: none;
}

.cupboards-types__count {
  display: flex;
}

.cupboards-types__item {
  position: relative;
  cursor: pointer;

  &:hover::before,
  &:focus::before,
  &:active::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba($basic-black, 0.5);
  }

  &:active::before {
    background: rgba($basic-black, 0.7);
  }

  &:hover .cupboards-types__price,
  &:focus .cupboards-types__price,
  &:active .cupboards-types__price {
    display: block;
  }

  &:hover .cupboards-types__count-text,
  &:focus .cupboards-types__count-text,
  &:active .cupboards-types__count-text {
    display: inline-block;
  }

  &:hover .cupboards-types__heading,
  &:focus .cupboards-types__heading,
  &:active .cupboards-types__heading {
    margin: 0 0 20px 0;
  }
}

.cupboards-types__count-text {
  @include font(Gilroy, 500, normal, 13px, normal, $basic-white);
  display: none;
  letter-spacing: -0.02em;
  text-align: right;
  max-width: 118px;
  padding-top: 7px;
  margin-right: 18px;
}
