.portfolio__wrapper {
  max-width: 1305px;
  margin: 0 auto;
  padding: 84px 75px;
  position: relative;
}

.portfolio__heading {
  @include font(Gilroy, 500, normal, 48px, normal, $basic-black);
  letter-spacing: -1px;
  margin: 0 0 37px 0;
}

.portfolio__list {
  display: flex;
  list-style: none;
  justify-content: space-between;
  padding: 0;
  margin: 0;
}

.portfolio .swiper-pagination-bullet-active {
  background: $basic-black;
}

.portfolio .swiper-button-next,
.portfolio .swiper-button-prev {
  color: $basic-black;
  opacity: 0.5;
}

.portfolio .swiper-button-next:hover,
.portfolio .swiper-button-prev:hover,
.portfolio .swiper-button-next:focus,
.portfolio .swiper-button-prev:focus {
  color: $basic-accent;
  opacity: 1;
}

.portfolio .swiper-button-next:active,
.portfolio .swiper-button-prev:active{
  color: $basic-accent;
  opacity: 0.7;
}

