.callback-form {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  background-color: $callback-form-background;
  padding: 36px 0 36px 62px;
  filter: drop-shadow(0 5px 40px rgba($cupboards-background, 0.4));
}

.callback-form__heading {
  @include font(Gilroy, normal, normal, 42px, 45px, $basic-black);
  max-width: 380px;
  letter-spacing: -1px;
  margin: 0 0 30px 0;
}

.callback-form__text {
  margin-right: 40px;
}

.callback-form__list {
  list-style: none;
  max-width: 403px;
  padding: 0 0 0 39px;
  margin: 0;
}

.callback-form__item {
  @include font(Gilroy, normal, normal, 16x, normal, $basic-black);
  position: relative;
  letter-spacing: -0.02em;
  margin-bottom: 18px;

  &:first-child {
    max-width: 322px;
  }

  &::before {
    position: absolute;
    content: '';
    background-image: url("./../img/form-ellipse.png");
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    top: 3px;
    left: -36px;
  }
}

.callback-form__wrapper {
  display: flex;
  padding-top: 14px;
}

.callback-form__wrapper form {
  display: flex;
  flex-direction: column;
}

.callback-form__name-input,
.callback-form__phone-input {
  @include font(Gilroy, normal, normal, 16x, 41px, $basic-black);
  border: 1px solid $input-border;
  border-radius: 10px;
  letter-spacing: -0.02em;
  padding: 6px 24px 7px 24px;
  margin-bottom: 10px;
  width: 359px;
}

.callback-form__phone-input  {
  margin-bottom: 13px;
}

.callback-form__legal {
  @include font(Gilroy, normal, normal, 12px, normal, $basic-black);
  max-width: 292px;
  margin: 0;
  letter-spacing: -0.02em;
}

.callback-form__manager {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 14px 22px;
}

.callback-form__manager img {
  margin-bottom: 17px;
}

.callback-form__manager-name {
  @include font(Gilroy, bold, normal, 22px, normal, $basic-black);
  letter-spacing: -0.02em;
  margin-bottom: 6px;
}

.callback-form__manager-function {
  @include font(Gilroy, normal, normal, 17px, normal, $basic-black);
  letter-spacing: -0.02em;
}
